import React from 'react';

class Result extends React.Component {
    bmiAnalysis = (bmi) => {
        if (bmi >= 30) {
            document.body.style.backgroundImage = 'linear-gradient( 107.2deg,  rgba(150,15,15,1) 10.6%, rgba(247,0,0,1) 91.1% )';
            return 'Obese';
        }
        else if (bmi >= 25) {
            document.body.style.backgroundImage = 'radial-gradient( circle farthest-corner at 10% 20%,  rgba(205,33,42,1) 0%, rgba(236,95,5,1) 90% )';
            return 'Overweight';
        }
        else if ((bmi < 25) && (bmi > 18.5)) {
            document.body.style.backgroundImage = 'linear-gradient( 109.6deg,  rgba(98,210,141,0.88) 11.2%, rgba(234,245,45,0.79) 88% )';
            return 'Healthy';
        }
        else {
            document.body.style.backgroundImage = 'radial-gradient( circle farthest-corner at 10% 20%,  rgba(205,33,42,1) 0%, rgba(236,95,5,1) 90% )';
            return 'Underweight';
        }
    }
    render() {
        return (
            <div>
                {this.bmiAnalysis(this.props.bmiResult)}
                <p>Score: {this.props.bmi}</p>
            </div>
        );
    }
}

export default Result;