import React from 'react';
import Result from '../Result/Result';
import classes from './Form.module.css';

class Form extends React.Component {
    state = {
        weight: 137,
        height: 0,
        bmi: 0
    }

    handleWeight = (event) => {
        this.setState({ weight: event.target.value });
    }

    toFeet(n) {
        let feet = n / 12;
        let feetCalc = Math.floor(feet.toFixed(1));
        let inches = n % 12;
        return feetCalc + inches;
    }

    handleHeight = (event) => {
        let heightCalc = this.toFeet(event.target.value);
        this.setState({ height: event.target.value })
        this.setState({ heightCalc: heightCalc })
    }

    calcBMI = async () => {
        let squaredHeight = this.state.height * this.state.height;
        let imperialFormula = 703 * (this.state.weight / squaredHeight);
        console.log(this.state.bmi);
        await this.setState({ bmi: imperialFormula.toFixed(2) });
        console.log(this.state.bmi);
    }

    render() {
        return (
            <div className={classes.bmiContainer}>
                <div className={classes.formGroup}>
                    <div className="row">
                        <div className="col-12">
                            <h1>{this.state.bmi !== 0 ? (<Result bmi={this.state.bmi} bmiResult={this.state.bmi} />) : this.props.title}</h1>
                        </div>
                        <div className="col-9">
                            <div className="weight">
                                <input type="range" min="2" max="211" className="value_range" id="weightRange" onChange={this.handleWeight} />
                            </div>
                        </div>
                        <div className="col-3">
                            <div className="label-weight field-text">
                                {this.state.weight}</div>
                        </div>
                        <div className="col-9">
                            <div className="height">
                                <input type="range" min="2" max="96" className="value_range" id="heightRange" onChange={this.handleHeight} />
                            </div>
                        </div>
                        <div className="col-3">
                            <div className="label-height field-text">
                                {this.state.height}</div>
                        </div>
                        <div className="col">
                            <button type="submit" className={classes.defaultBtn} onClick={this.calcBMI}>GO!</button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Form;